import { Injectable ,OnInit} from '@angular/core';
import { HttpClient, HttpRequest, HttpHeaders,HttpParams } from '@angular/common/http';

import { AppSettings } from '../appSetting'
import { User } from '../_models';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { stringify } from '@angular/compiler/src/util';
import { promise } from 'selenium-webdriver';

@Injectable({ providedIn: 'root' })
export class DataService implements OnInit{
    token;
    constructor(private http: HttpClient) { }
    ngOnInit(){}
    ///////////////////////////////////////User Profile//////////////////////////////////////////
   
    public handleError(error: any): Promise<any>{
		return error;
	}

    adminget(endpoint): Promise<any>
	{
        this.token='Bearer ' + localStorage.getItem("admintoken");
		return this.http.get(`${AppSettings.base_url}` + endpoint, { headers: { Authorization: this.token } })
			.toPromise().then((response: any) =>
			{
				if (response.status === 401) 
				{
					 localStorage.clear();
					window.location.href="#/pages/login";
				}
				else 
				{
					return response;
				}
			},
				(reason: any) =>
				{
					if (reason.error.status === 401) 
					{
						  localStorage.clear();
						  window.location.href="#/pages/login";
					}
					return reason;

				}).catch(this.handleError);
	}

    get(endpoint): Promise<any>
	{
        this.token='Bearer ' + localStorage.getItem("token");
		return this.http.get(`${AppSettings.base_url}` + endpoint, { headers: { Authorization: this.token } })
			.toPromise().then((response: any) =>
			{
				if (response.status === 401) 
				{
					 localStorage.clear();
					window.location.href="";
				}
				else 
				{
					return response;
				}
			},
				(reason: any) =>
				{
					if (reason.error.status === 401) 
					{
						   localStorage.clear();
						window.location.href="";

					}
					return reason;

				}).catch(this.handleError);
	}

	adminpost(endpoint, body): Promise<any>
	{
        this.token='Bearer ' + localStorage.getItem("admintoken");
		return this.http.post(`${AppSettings.base_url}` + endpoint, body, { headers: { Authorization: this.token } })
			.toPromise().then((response: any) =>
			{
				if (response.status === 401) 
				{				
							  localStorage.clear();

					window.location.href="#/pages/login";

				}
				else 
				{
					return response;
				}
			},
				(reason: any) =>
				{
					if (reason.error.status === 401) 
					{		

						  localStorage.clear();	
					 	window.location.href="#/pages/login";

					}
					return reason;

				}).catch(this.handleError);
	}


	post(endpoint, body): Promise<any>
	{
        this.token='Bearer ' + localStorage.getItem("token");
		return this.http.post(`${AppSettings.base_url}` + endpoint, body, { headers: { Authorization: this.token } })
			.toPromise().then((response: any) =>
			{
				if (response.status === 401) 
				{
					localStorage.clear();
						  window.location.href="#/pages/login";
				}
				else 
				{
					return response;
				}
			},
				(reason: any) =>
				{
					if (reason.error.status === 401) 
					{
						localStorage.clear();
						  window.location.href="#/pages/login";
					}
					return reason;

				}).catch(this.handleError);
	}

    updateprofile(userinfo,auth_key) {
        return this.http.post(`${AppSettings.base_url}dashboard/updateProfile`, userinfo,{headers: { 
            'Authorization': auth_key
          }}).pipe(map(data=>{
            return data;
        })
        );
    }
    
    getallApplcant(status:string,auth_key) : any {
       
        return this.http.get(`${AppSettings.base_url}dashboard/getAllApplication?status=${status}`,{ headers: { 
            'Authorization': auth_key
          }});
    }

    loandetailsid(id:number,loantype:string,auth_key:string):any {
    
        return this.http.get(`${AppSettings.base_url}dashboard/getApplicationDetails?id=`+id+'&loan_type='+loantype,{ headers: { 
        'Authorization': auth_key
        }});
    }

    getallquestionList(id:number): any {
        return this.http.get(`${AppSettings.base_url}dashboard/getEmplyerQuestion?employer_question_id=${id}`);
    }

    Getfeeback(id: number,auth_key:string): any {
        return this.http.get(`${AppSettings.base_url}dashboard/getFeedBack?loan_application_id=${id}`,{ headers: { 
            'Authorization': auth_key
          }});
    }

    Getemployerquestion(id: number,auth_key:string): any {
        return this.http.get(`${AppSettings.base_url}dashboard/getEmplyerQuestion?loan_application_id=${id}`,{ headers: { 
            'Authorization': auth_key
          }});
    }

    getloaninstallmentPlan(id:number,auth_key:string):any {
        return this.http.get(`${AppSettings.base_url}dashboard/getApplicationDetails?id=`+id+'&loan_type='+'mortgage',{ headers: { 
         'Authorization': auth_key
       }});
    }

    sendEmployersConfirmation(emp,auth_key:string) {
        return this.http.post(`${AppSettings.base_url}dashboard/sendEmployersConfirmation`,emp,{ headers: { 
            'Authorization': auth_key
          }})
            .pipe(map(data => {
                return data;
            }));
    }

     /////////////////lendMoney /////////////////////
    updateLoanStatus(lendmoneyData,auth_key):any {
        return this.http.post(`${AppSettings.base_url}dashboard/updateLoanStatus`, lendmoneyData,{headers: { 
            'Authorization': auth_key
          }});
    }

}