import { Component, OnInit } from '@angular/core';
import PerfectScrollbar from 'perfect-scrollbar';

declare const $: any;

//Metadata
export interface RouteInfo {
    path: string;
    title: string;
    type: string;
    icontype: string;
    collapse?: string;
    children?: ChildrenItems[];
}

export interface ChildrenItems {
    path: string;
    title: string;
    ab: string;
    type?: string;
}

//Menu Items
export const ROUTES: RouteInfo[] = [{
        path: '/admin/dashboard',
        title: 'Dashboard',
        type: 'link',
        icontype: './assets/Icons/AdminportalIcons/das.png'
    }
    ,{
        path: '/admin',
        title: 'Users',
        type: 'sub',
        icontype: './assets/Icons/AdminportalIcons/user.png',
        collapse: 'components',
        children: [
            {path: 'host', title: 'Host', ab:'H'},
            {path: 'guest', title: 'Guest', ab:'G'},
    
        ]
    },{
        path: '/admin/subscriptions',
        title: 'Subscriptions',
        type: 'link',
        icontype: './assets/Icons/AdminportalIcons/sub.png',

    }
    ,{
        path: '/admin',
        title: 'Offers',
        type: 'sub',
        icontype: './assets/Icons/AdminportalIcons/Offers.png',
        collapse: 'forms',
        children: [
            {path: 'booking', title: 'Booking Offers', ab:'BO'},
            {path: 'normal', title: 'Normal Offers', ab:'NO'},
        ]
    },
    // {
    //         path: '/admin/languagesetting',
    //         title: 'Language Setting',
    //         type: 'link',
    //         icontype: './assets/Icons/AdminportalIcons/setting.png',
    
    //      }
    //      ,
         {
                 path: '/admin/faq',
                 title: 'FAQ',
                 type: 'link',
                 icontype: './assets/Icons/AdminportalIcons/FAQ.png',
         
              }
         ,
    {
            path: '/admin/category',
            title: 'Blogs Category',
            type: 'link',
            icontype: './assets/Icons/AdminportalIcons/category blog.png',
    
         }
         ,
         {
                 path: '/admin/blog',
                 title: 'Blogs',
                 type: 'link',
                 icontype: './assets/Icons/AdminportalIcons/Blog.png',
         
              },
              {
                path: '/admin/feedback',
                title: 'Feedback',
                type: 'link',
                icontype: './assets/Icons/AdminportalIcons/FAQ.png',
        
             }
    
];
@Component({
    selector: 'app-sidebar-cmp',
    templateUrl: 'sidebar.component.html',
})

export class SidebarComponent implements OnInit {
    public menuItems: any[];
    ps: any;
    isMobileMenu() {
        if ($(window).width() > 991) {
            return false;
        }
        return true;
    };

    ngOnInit() {
        this.menuItems = ROUTES.filter(menuItem => menuItem);
        if (window.matchMedia(`(min-width: 960px)`).matches && !this.isMac()) {
            const elemSidebar = <HTMLElement>document.querySelector('.sidebar .sidebar-wrapper');
            this.ps = new PerfectScrollbar(elemSidebar);
        }
    }
    updatePS(): void  {
        if (window.matchMedia(`(min-width: 960px)`).matches && !this.isMac()) {
            this.ps.update();
        }
    }
    isMac(): boolean {
        let bool = false;
        if (navigator.platform.toUpperCase().indexOf('MAC') >= 0 || navigator.platform.toUpperCase().indexOf('IPAD') >= 0) {
            bool = true;
        }
        return bool;
    }
}
