import { Routes } from '@angular/router';

import { AdminLayoutComponent } from './Admin/layouts/admin/admin-layout.component';
import { Admin_layoutComponent } from './Admin/layouts/admin_layout/admin_layout.component';
import { AuthLayoutComponent } from './Admin/layouts/auth/auth-layout.component';
import { GuestLayoutComponent } from './Admin/layouts/guest/guest-layout.component';
import { HostLayoutComponent } from './Admin/layouts/host/host-layout.component';
import { HostportalLayoutComponent } from './Admin/layouts/Hostportal/Hostportal-layout.component';

export const AppRoutes: Routes = [
    {
      path: '',
      redirectTo: ' ',
      pathMatch: 'full',
    },
    {
      path: '',
      component: AuthLayoutComponent,
      children: [{
            path: '',
            loadChildren: () => import('./home/home.module').then(m => m.HomeModule)
        }
      ]
    }, {
      path: 'admin',
      component: AdminLayoutComponent,
      children: [{
            path: '',
            loadChildren: () => import('./Admin/Pages/home.module').then(m => m.HomeModule)
        }
      ]
    }, {
      path: '',
      component: Admin_layoutComponent,
      children: [{
        path: 'pages',
        loadChildren: () => import('./pages/pages.module').then(m => m.PagesModule)
      }
    ]
    },
    {
      path: '',
      component: AuthLayoutComponent,
      children: [
      {
        path: 'Website',
        loadChildren: () => import('./Website/Website.module').then(m => m.WebsiteModule)
      }]
    },
    {
      path: '',
      component: HostLayoutComponent,
      children: [
      {
        path: 'host',
        loadChildren: () => import('./Host/Host.module').then(m => m.HostModule)
      }]
    }
    ,{
    path: '',
    component: HostportalLayoutComponent,
    children: [
    {
      path: 'hostportal',
      loadChildren: () => import('./Host/Hostportal.module').then(m => m.HostportalModule)
    }]
  },
  {
    path: '',
    component: GuestLayoutComponent,
    children: [
    {
      path: 'guest',
      loadChildren: () => import('./Guest/Guest.module').then(m => m.GuestModule)
    }]
  },
  {
    path: '',
    component: GuestLayoutComponent,
    children: [
    {
      path: 'guestportal',
      loadChildren: () => import('./Guest/Guestportal.module').then(m => m.GuestportalModule)
    }]
  }
];
