import { Component, OnInit, ElementRef } from '@angular/core';
import { Router, NavigationEnd, NavigationStart } from '@angular/router';
import { Subscription } from 'rxjs/Subscription';
import { TranslationService } from '../../../translation.service';
// import { AuthenticationService } from 'src/app/_services';


import { locale as englishLang } from '../../../i18n/en';
import { locale as frenchLang } from '../../../i18n/fr';
import { locale as germanLang } from '../../../i18n/gr';
import { locale as italianLang } from '../../../i18n/it';
import { locale as spanishLang } from '../../../i18n/sp';
import { locale as chineseLang } from '../../../i18n/ch';
@Component({
  selector: 'app-layout',
  templateUrl: './auth-layout.component.html'
})
export class AuthLayoutComponent implements OnInit {
  private toggleButton: any;
  private sidebarVisible: boolean;
  mobile_menu_visible: any = 0;
  private _router: Subscription;
  currentLang='en';
user;
pic;
  constructor(private router: Router, private element: ElementRef, private translationService: TranslationService) {
      this.sidebarVisible = false;
      this.translationService.addTranslation(englishLang, frenchLang,germanLang,italianLang,spanishLang,chineseLang)

  }
  ngOnInit(){

    if(localStorage.getItem("currentlang")==null)
    {
      localStorage.setItem("currentlang",this.currentLang);
      this.currentLang=localStorage.getItem("currentlang");

    }else{
      this.currentLang=localStorage.getItem("currentlang");
    }
    this.pic=localStorage.getItem('pic');
    if(localStorage.getItem("currentUser")!=null)
    {
      this.user=JSON.parse(localStorage.getItem("currentUser"));
     console.log(this.user);
    }

    const navbar: HTMLElement = this.element.nativeElement;

    this.toggleButton = navbar.getElementsByClassName('navbar-toggler')[0];
    this._router = this.router.events.filter(event => event instanceof NavigationEnd).subscribe((event: NavigationEnd) => {
      this.sidebarClose();
      const $layer = document.getElementsByClassName('close-layer')[0];
      if ($layer) {
        $layer.remove();
      }
    });
  }
  sidebarOpen() {
    var $toggle = document.getElementsByClassName('navbar-toggler')[0];
      const toggleButton = this.toggleButton;
      const body = document.getElementsByTagName('body')[0];
      setTimeout(function(){
          toggleButton.classList.add('toggled');
      }, 500);
      body.classList.add('nav-open');
      setTimeout(function() {
          $toggle.classList.add('toggled');
      }, 430);

      var $layer = document.createElement('div');
      $layer.setAttribute('class', 'close-layer');


      if (body.querySelectorAll('.wrapper-full-page')) {
          document.getElementsByClassName('wrapper-full-page')[0].appendChild($layer);
      }else if (body.classList.contains('off-canvas-sidebar')) {
          document.getElementsByClassName('wrapper-full-page')[0].appendChild($layer);
      }

      setTimeout(function() {
          $layer.classList.add('visible');
      }, 100);

      $layer.onclick = function() { //asign a function
        body.classList.remove('nav-open');
        this.mobile_menu_visible = 0;
        this.sidebarVisible = false;

        $layer.classList.remove('visible');
        setTimeout(function() {
            $layer.remove();
            $toggle.classList.remove('toggled');
        }, 400);
      }.bind(this);

      body.classList.add('nav-open');
      this.mobile_menu_visible = 1;
      this.sidebarVisible = true;
  };
  sidebarClose() {
    var $toggle = document.getElementsByClassName('navbar-toggler')[0];
      const body = document.getElementsByTagName('body')[0];
      this.toggleButton.classList.remove('toggled');
      var $layer = document.createElement('div');
      $layer.setAttribute('class', 'close-layer');

      this.sidebarVisible = false;
      body.classList.remove('nav-open');
      // $('html').removeClass('nav-open');
      body.classList.remove('nav-open');
      if ($layer) {
          $layer.remove();
      }

      setTimeout(function() {
          $toggle.classList.remove('toggled');
      }, 400);

      this.mobile_menu_visible = 0;
  };
  sidebarToggle() {
      if (this.sidebarVisible === false) {
          this.sidebarOpen();
      } else {
          this.sidebarClose();
      }
  }
  
  logout(){
    // this.authService.logout();
    window.location.href="";
    localStorage.clear()
    window.location.reload();
  }

  getGuest(){
    this.router.navigate(["/guest/home"]);
  }

  getHost(){
    this.router.navigate(["/host/home"]);
  }

  onLangChange(currentLang) {
    localStorage.setItem("currentlang",currentLang.value)
    this.translationService.useLang(currentLang.value);
  }
  languages = [
    { name: 'English', lang: 'en' },
    { name: 'french', lang: 'fr' },
    { name: 'German', lang: 'gr' },
    { name: 'Italian', lang: 'it' }
    // { name: 'Spanisch', lang: 'sp' },
    // { name: 'Chinesisch', lang: 'ch' }
  ]
}
