import { Component, OnInit, ElementRef, ChangeDetectorRef, ViewChild, ChangeDetectionStrategy } from '@angular/core';
import { FormBuilder, FormGroup, NgForm, Validators } from '@angular/forms';
import { Router, NavigationEnd, NavigationStart } from '@angular/router';
import { Subscription } from 'rxjs/Subscription';
import { AuthenticationService } from 'src/app/_services';
import { DataService } from 'src/app/_services/data.services';
import { AppleLoginService } from 'src/app/_services/apple.service';
import {
  CountryISO,
  SearchCountryField
} from "ngx-intl-tel-input";
import Swal from 'sweetalert2';
declare var $: any;
import { SocialAuthService, FacebookLoginProvider, SocialUser, GoogleLoginProvider } from 'angularx-social-login';
import { initializeApp } from "firebase/app";
import { getAnalytics } from "firebase/analytics";
import { getMessaging, getToken, onMessage } from "firebase/messaging";
import { onBackgroundMessage } from "firebase/messaging/sw";
import * as moment from 'moment';

@Component({
  selector: 'app-layout',
  templateUrl: './guest-layout.component.html'
})

export class GuestLayoutComponent implements OnInit {
  private toggleButton: any;
  private sidebarVisible: boolean;
  mobile_menu_visible: any = 0;
  private _router: Subscription;
  step = 0;
  user = null;
  showAlert = false;
  errorMessage = ''
  url = null;
  role = null;
  forgot_password_email = null;
  passtext = "password";
  ishidden = true;
  passtext1 = "password";
  ishidden1 = true;
  page = 0;
  notifications = [];
  unread_notifications = 0;
  new_notification_status = false;
  moment: any = moment;

  socialUser: SocialUser;
  isLoggedin: boolean = null;
  pic;
  @ViewChild('registerNgForm') registerNgForm: NgForm;
  registerForm: FormGroup;
  @ViewChild('loginNgForm') loginNgForm: NgForm;
  loginForm: FormGroup;
  @ViewChild('verifyEmailNgForm') verifyEmailNgForm: NgForm;
  verifyEmailForm: FormGroup;
  @ViewChild('verifyForgotPasswordOtpNgForm') verifyForgotPasswordOtpNgForm: NgForm;
  verifyForgotPasswordOtpForm: FormGroup;
  @ViewChild('updatePasswordNgForm') updatePasswordNgForm: NgForm;
  updatePasswordForm: FormGroup;
  @ViewChild('planNgForm') planNgForm: NgForm;
  planForm: FormGroup;
  plan_id = null;
  level = null;
  plans = null;
  paypalInterval = null;

  separateDialCode = false;
  SearchCountryField = SearchCountryField;
  CountryISO = CountryISO;
  preferredCountries: CountryISO[] = [
    CountryISO.UnitedStates,
    CountryISO.UnitedKingdom
  ];

  fcm_token = null;

  constructor(private router: Router,
    private _formBuilder: FormBuilder,
    private cdf: ChangeDetectorRef,
    private authService: AuthenticationService,
    private dataService: DataService,
    private element: ElementRef,
    private socialAuthService: SocialAuthService,
    private appleService: AppleLoginService) {
    this.sidebarVisible = false;
  }

  ngOnInit() {
    this.url = this.router.url
    // Initialize Firebase
    this.pic=localStorage.getItem('pic');

    const firebaseConfig = {
      apiKey: "AIzaSyALplqbC_JECI84zM4OSYV0zhmzlrml46s",
      authDomain: "fresh-rain-320914.firebaseapp.com",
      projectId: "fresh-rain-320914",
      storageBucket: "fresh-rain-320914.appspot.com",
      messagingSenderId: "312394151153",
      appId: "1:312394151153:web:fe030c2ad8682ba160e550",
      measurementId: "G-2SDBP32RL6"
    };
    const app = initializeApp(firebaseConfig);

    const messaging = getMessaging();
    getToken(messaging, { vapidKey: 'BPupbI-8TFUeiPhfTI43JW5BZ7uDd3vAqba1w1YHR3H8mqIswifdGByxr0Vbz0ENqjJyFjOi0ptyqM5w3wBH3bc' })
      .then((currentToken) => {
        if (currentToken) {
          localStorage.setItem('fcm_token', currentToken)
          this.fcm_token = currentToken
          // Track the token -> client mapping, by sending to backend server
          // show on the UI that permission is secured
        } else {
          console.log('No registration token available. Request permission to generate one.');
          // shows on the UI that permission is required 
        }
      }).catch((err) => {
        console.log('An error occurred while retrieving token. ', err);
        // catch error while creating client token
      });

    if (localStorage.getItem("token") != null) {
      this.user = 'exists';
      this.role = localStorage.getItem('role')
    }

    this.getNotifications();

    onMessage(messaging, (payload) => {
      if (localStorage.getItem("token") != null) {
        this.getNotifications();
      }
    });

    const navbar: HTMLElement = this.element.nativeElement;

    this.toggleButton = navbar.getElementsByClassName('navbar-toggler')[0];
    this._router = this.router.events.filter(event => event instanceof NavigationEnd).subscribe((event: NavigationEnd) => {
      this.sidebarClose();
      const $layer = document.getElementsByClassName('close-layer')[0];
      if ($layer) {
        $layer.remove();
      }
    });

    this.registerForm = this._formBuilder.group({
      fname: null,
      lname: null,
      email: [null, Validators.email],
      password: '',
      role: 'guest',
      contact_no: null,
      address: null,
      website: null,
      vat_no: null,
      pic: null
    });

    this.loginForm = this._formBuilder.group({
      email: [null, Validators.email],
      password: '',
    })

    this.verifyEmailForm = this._formBuilder.group({
      email: null
    });

    this.verifyForgotPasswordOtpForm = this._formBuilder.group({
      email: null,
      otp: null
    });

    this.updatePasswordForm = this._formBuilder.group({
      email: null,
      password: null,
      confirm_password: null
    })

    this.planForm = this._formBuilder.group({
      plan_id: null,
      type: 'card',
      number: null,
      month: null,
      year: null,
      cvc: null
    })

    this.socialAuthService.authState.subscribe((user) => {
      this.socialUser = user;
      this.isLoggedin = (user != null);
    });

  }

  getNotifications() {
    this.new_notification_status = false;
    if (this.notifications.length < 10) {
      this.page = 1;
    }
    else {
      this.page = this.page + 1;
    }
    if(localStorage.getItem("token")==null)
    {
      return;
    }
    this.dataService.get('get-notification/' + this.page).then((result) => {
      for (let i = 0; i < result.data.length; i++) {
        this.notifications.push(result.data[i])
        if (result.data[i].read_status == 0) {
          this.unread_notifications = this.unread_notifications + 1;
        }
      }
      if (result.data == []) {
        this.new_notification_status = true;
      }
      this.cdf.detectChanges()
    })
  }

  sidebarOpen() {
    var $toggle = document.getElementsByClassName('navbar-toggler')[0];
    const toggleButton = this.toggleButton;
    const body = document.getElementsByTagName('body')[0];
    setTimeout(function () {
      toggleButton.classList.add('toggled');
    }, 500);
    body.classList.add('nav-open');
    setTimeout(function () {
      $toggle.classList.add('toggled');
    }, 430);

    var $layer = document.createElement('div');
    $layer.setAttribute('class', 'close-layer');


    if (body.querySelectorAll('.wrapper-full-page')) {
      document.getElementsByClassName('wrapper-full-page')[0].appendChild($layer);
    } else if (body.classList.contains('off-canvas-sidebar')) {
      document.getElementsByClassName('wrapper-full-page')[0].appendChild($layer);
    }

    setTimeout(function () {
      $layer.classList.add('visible');
    }, 100);

    $layer.onclick = function () { //asign a function
      body.classList.remove('nav-open');
      this.mobile_menu_visible = 0;
      this.sidebarVisible = false;

      $layer.classList.remove('visible');
      setTimeout(function () {
        $layer.remove();
        $toggle.classList.remove('toggled');
      }, 400);
    }.bind(this);

    body.classList.add('nav-open');
    this.mobile_menu_visible = 1;
    this.sidebarVisible = true;
  };

  sidebarClose() {
    var $toggle = document.getElementsByClassName('navbar-toggler')[0];
    const body = document.getElementsByTagName('body')[0];
    this.toggleButton.classList.remove('toggled');
    var $layer = document.createElement('div');
    $layer.setAttribute('class', 'close-layer');

    this.sidebarVisible = false;
    body.classList.remove('nav-open');
    // $('html').removeClass('nav-open');
    body.classList.remove('nav-open');
    if ($layer) {
      $layer.remove();
    }

    setTimeout(function () {
      $toggle.classList.remove('toggled');
    }, 400);

    this.mobile_menu_visible = 0;
  };

  sidebarToggle() {
    if (this.sidebarVisible === false) {
      this.sidebarOpen();
    } else {
      this.sidebarClose();
    }
  }

  logout() {
    const data = {
      fcm_token: localStorage.getItem('fcm_token')
    }
    this.dataService.post('logout', data).then((result) => { //verify register otp
      this.user = null;
      this.isLoggedin = null;
      this.pic = null;

      localStorage.clear();
      this.cdf.detectChanges();
      this.router.navigate(['/guest/home'])
    })
  }

  login() {
    this.showAlert = false;
    if (this.loginForm.invalid) {
      Swal.fire({
        position: 'center',
        icon: 'error',
        text: 'Please Fill All Required Fields',
        showConfirmButton: false,
        timer: 2500
      })
      this.cdf.detectChanges()
    }

    const formData = this.loginForm.getRawValue();

    const data = {
      email: formData.email,
      password: formData.password,
      fcm_token: this.fcm_token
    };

    this.authService.post('login', data).then((result) => {
      if (result.success == true) {
        localStorage.setItem('token', result.data.token);
        localStorage.setItem('role', result.data.user.role);
        localStorage.setItem('id', result.data.user.id);
        localStorage.setItem('plan', result.data.user.plan);
        localStorage.setItem('pic', result.data.user.pic);

        this.router.navigate(['/guestportal/bookinglist']);
      }
      else {
        Swal.fire({
          position: 'center',
          icon: 'error',
          text: 'Wrong Credentials',
          showConfirmButton: false,
          timer: 2500
        })
      }
      this.cdf.detectChanges();
    },
      error => {
        Swal.fire({
          position: 'center',
          icon: 'error',
          text: 'Wrong Credentials',
          showConfirmButton: false,
          timer: 2500
        })
        formData.password = null
        this.cdf.detectChanges();
      }
    )
  }

  registerValidation() {
    if (this.registerForm.invalid) {
      const invalid = [];
      const controls = this.registerForm.controls;
      for (const name in controls) {
        if (controls[name].invalid) {
          invalid.push(name);
        }
      }
      if(invalid[0] == 'fname'){
        Swal.fire({
          position: 'center',
          icon: 'error',
          text: 'Please Provide First Name With Minimum 4 Chars At Least',
          showConfirmButton: false,
          timer: 2500
        })
      }
      else if(invalid[0] == 'lname'){
        Swal.fire({
          position: 'center',
          icon: 'error',
          text: 'Please Provide Last Name With Minimum 4 Chars At Least',
          showConfirmButton: false,
          timer: 2500
        })
      }
      else if(invalid[0] == 'contact_no'){
        Swal.fire({
          position: 'center',
          icon: 'error',
          text: 'Please Provide Valid Phone Number. You may see placeholder for phone number format.',
          showConfirmButton: false,
          timer: 2500
        })
      }
      else if(invalid[0] == 'website'){
        Swal.fire({
          position: 'center',
          icon: 'error',
          text: 'Please Provide Valid URL. You may see placeholder for website format.',
          showConfirmButton: false,
          timer: 2500
        })
      }
      this.cdf.detectChanges()
      return
    }

    this.step = 2
    this.cdf.detectChanges()
  }

  register() {
    this.showAlert = false;

    let otp = $('#otp').val()
    if (otp == null || otp == '') {
      Swal.fire({
        position: 'center',
        icon: 'error',
        text: 'Please Provide OTP Code',
        showConfirmButton: false,
        timer: 2500
      })
      this.cdf.detectChanges()
      return
    }

    const formData = this.registerForm.getRawValue();

    const otpData = {
      email: formData.email,
      otp: otp
    }

    const data = {
      fname: formData.fname,
      lname: formData.lname,
      email: formData.email,
      password: formData.password,
      role: 'guest',
      contact_no: formData.contact_no.nationalNumber,
      country_calling_code: formData.contact_no.dialCode,
      country_code: formData.contact_no.countryCode,
      address: formData.address,
      website: formData.website,
      vat_no: formData.vat_no,
      pic: null,
      fcm_token: this.fcm_token
    };

    this.dataService.post('test-another', otpData).then((result) => { //verify register otp
      if (result.success == true) {
        this.authService.post('registration', data).then((result) => { //register
          if (result.success == true) {
            localStorage.setItem('token', result.data.token);
            localStorage.setItem('role', result.data.user.role);
            localStorage.setItem('id', result.data.user.id);
            this.router.navigate(['/guestportal/bookinglist']);
          }
          else {
            Swal.fire({
              position: 'center',
              icon: 'error',
              text: 'Something went wrong. Please try again.',
              showConfirmButton: false,
              timer: 2500
            })
          }
          this.cdf.detectChanges();
        },
          error => {
            Swal.fire({
              position: 'center',
              icon: 'error',
              text: 'Something went wrong. Please try again.',
              showConfirmButton: false,
              timer: 2500
            })
            this.cdf.detectChanges();
          }
        )
      }
      else {
        Swal.fire({
          position: 'center',
          icon: 'error',
          text: 'OTP code not valid.',
          showConfirmButton: false,
          timer: 2500
        })
      }
      this.cdf.detectChanges()
    })

  }

  signinModal() {
    this.showAlert = false;
    this.errorMessage = null;
    $(".bd-example-modal-lg1").modal('show');
    $(".signupModal").modal('hide');
    this.registerForm = this._formBuilder.group({
      fname: null,
      lname: null,
      email: [null, Validators.email],
      password: '',
      role: 'guest',
      contact_no: null,
      address: null,
      website: null,
      vat_no: null,
      pic: null
    });
  }

  forgotPasswordModal() {
    this.showAlert = false;
    this.errorMessage = null;
    $(".bd-example-modal-lg1").modal('hide');
    $("#forgot-password").modal('toggle');
    this.loginForm = this._formBuilder.group({
      email: [null, Validators.email],
      password: '',
    })
  }

  signupModal() {
    this.showAlert = false;
    this.errorMessage = null;
    $(".bd-example-modal-lg1").modal('hide');
    $(".signupModal").modal('show');
    this.loginForm = this._formBuilder.group({
      email: [null, Validators.email],
      password: '',
    })
  }

  sendOTP() {
    this.showAlert = false;
    const formData = this.registerForm.getRawValue();

    if (formData.email == null) {
      Swal.fire({
        position: 'center',
        icon: 'error',
        text: 'Please Fill All Required Fields',
        showConfirmButton: false,
        timer: 2500
      })
      this.cdf.detectChanges()
    }
    else {
      const data = {
        email: formData.email
      }
      this.dataService.post('test-me', data).then((result) => {
        if (result.success == true) {
          this.step = 1;
        }
        else {
          Swal.fire({
            position: 'center',
            icon: 'error',
            text: result.error.message,
            showConfirmButton: false,
            timer: 2500
          })
        }
        this.cdf.detectChanges()
      })
    }
  }

  verifyEmail() {
    this.errorMessage = null;
    this.showAlert = false;
    if (this.verifyEmailForm.invalid) {
      Swal.fire({
        position: 'center',
        icon: 'error',
        text: 'Please Fill All Required Fields',
        showConfirmButton: false,
        timer: 2500
      })
      this.cdf.detectChanges()
      return
    }

    const formData = this.verifyEmailForm.getRawValue();
    this.forgot_password_email = formData.email;
    const data = {
      email: this.forgot_password_email
    }
    this.dataService.post('verify-email', data).then((result) => {
      if (result.success == true) {
        $('#vEmail').prop('hidden', true);
        $('#vForgotPasswordOtp').prop('hidden', false);
        this.verifyEmailForm.controls['email'].setValue(null)
      }
      else {
        Swal.fire({
          position: 'center',
          icon: 'error',
          text: 'Something went wrong. Please try again.',
          showConfirmButton: false,
          timer: 2500
        })
      }
      this.cdf.detectChanges();
    })
  }

  verifyForgoPasswordOtp() {
    this.errorMessage = null;
    this.showAlert = false;
    if (this.verifyForgotPasswordOtpForm.invalid) {
      Swal.fire({
        position: 'center',
        icon: 'error',
        text: 'Please Fill All Required Fields',
        showConfirmButton: false,
        timer: 2500
      })
      this.cdf.detectChanges()
      return
    }

    const formData = this.verifyForgotPasswordOtpForm.getRawValue();
    const data = {
      email: this.forgot_password_email,
      otp: formData.otp
    }
    this.dataService.post('verify-forgot-password-otp', data).then((result) => {
      if (result.success == true) {
        $('#vForgotPasswordOtp').prop('hidden', true);
        $('#uPassword').prop('hidden', false);
        this.verifyForgotPasswordOtpForm.controls['otp'].setValue(null)
      }
      else {
        Swal.fire({
          position: 'center',
          icon: 'error',
          text: 'Something went wrong. Please try again.',
          showConfirmButton: false,
          timer: 2500
        })
      }
      this.cdf.detectChanges()
    })
  }

  updatePasswordOtp() {
    this.errorMessage = null;
    this.showAlert = false;
    if (this.updatePasswordForm.invalid) {
      Swal.fire({
        position: 'center',
        icon: 'error',
        text: 'Please Fill All Required Fields',
        showConfirmButton: false,
        timer: 2500
      })
      this.cdf.detectChanges()
      return
    }

    const formData = this.updatePasswordForm.getRawValue();
    if (formData.password != formData.confirm_password) {
      Swal.fire({
        position: 'center',
        icon: 'error',
        text: 'Password does not match',
        showConfirmButton: false,
        timer: 2500
      })
      this.cdf.detectChanges()
      return
    }

    const data = {
      email: this.forgot_password_email,
      password: formData.password
    }
    this.dataService.post('update-password', data).then((result) => {
      if (result.success == true) {
        $('#uPassword').prop('hidden', true);
        $('#vEmail').prop('hidden', false);
        Swal.fire({
          position: 'center',
          icon: 'success',
          title: 'Success',
          showConfirmButton: false,
          timer: 2500
        });

        $("#forgot-password").modal('toggle');
        this.updatePasswordForm.controls['password'].setValue(null)
        this.updatePasswordForm.controls['confirm_password'].setValue(null)
        $(".bd-example-modal-lg1").modal('show');
      }
      else {
        Swal.fire({
          position: 'center',
          icon: 'error',
          text: 'Something went wrong. Please try again.',
          showConfirmButton: false,
          timer: 2500
        })
      }
      this.cdf.detectChanges()
    })
  }

  loginWithFacebook(): void {
    this.socialAuthService.signIn(FacebookLoginProvider.PROVIDER_ID).then((userData) => {
      this.socialUser = userData;
      const data = {
        fname: this.socialUser.firstName,
        lname: this.socialUser.lastName,
        email: this.socialUser.email,
        fcm_token: this.fcm_token
      }

      this.dataService.post('social-login', data).then((result) => {
        if (result.success == true) {
          localStorage.setItem('token', result.data.token);
          localStorage.setItem('role', result.data.user.role);
          localStorage.setItem('id', result.data.user.id);
          this.router.navigate(['/guestportal/bookinglist']);
        }
        else {
          Swal.fire({
            position: 'center',
            icon: 'error',
            text: 'Something went wrong. Please try agian.',
            showConfirmButton: false,
            timer: 2500
          })
        }
        this.cdf.detectChanges()
      });
    });
  }

  visitHost() {
    if (localStorage.getItem('plan') != null) {
      localStorage.setItem('role', 'host');
      this.router.navigate(['/hostportal/offerlist']);
    }
    else {
      this.dataService.get('host/get_plan').then((result) => {
        this.plans = result.data
        this.cdf.detectChanges()
      })
      $('.try-offers1').modal('show')
    }
  }

  buyPlan(id, level, amount) {
    this.plan_id = id
    this.level = level;
    this.showAlert = false;
    $('.try-offers1').modal('hide')
    $('#plan').modal('show')
  }

  subscribePlan() {
    this.showAlert = false;
    this.errorMessage = null;
    this.cdf.detectChanges();

    const formData = this.planForm.getRawValue();

    const data = {
      plan_id: this.plan_id.toString(),
      type: 'card',
      number: formData.number,
      exp_month: formData.month,
      exp_year: formData.year,
      cvc: formData.cvc
    }

    if ($('#stripe').is(':checked')) {
      this.dataService.post('guest/stripe_payment', data).then((result) => {
        if (result.success == 'Subscribe successfully.') {
          $('#plan').modal('hide')
          this.updatePlan();
          this.planForm = this._formBuilder.group({
            plan_id: null,
            type: 'card',
            number: null,
            month: null,
            year: null,
            cvc: null
          })
        }
        else {
          Swal.fire({
            position: 'center',
            icon: 'error',
            text: 'Something went wrong. Please try again.',
            showConfirmButton: false,
            timer: 2500
          })
          this.cdf.detectChanges()
          return
        }
      })
    }
  }

  signInWithGoogle(): void {
    this.socialAuthService.signIn(GoogleLoginProvider.PROVIDER_ID).then((userData) => {
      this.socialUser = userData;
      const data = {
        fname: this.socialUser.firstName,
        lname: this.socialUser.lastName,
        email: this.socialUser.email,
        fcm_token: this.fcm_token
      }

      this.dataService.post('social-login', data).then((result) => {
        if (result.success == true) {
          localStorage.setItem('token', result.data.token);
          localStorage.setItem('role', result.data.user.role);
          localStorage.setItem('id', result.data.user.id);
          this.router.navigate(['/guestportal/bookinglist']);
        }
        else {
          Swal.fire({
            position: 'center',
            icon: 'error',
            text: 'Something went wrong. Please try agian.',
            showConfirmButton: false,
            timer: 2500
          })
        }
        this.cdf.detectChanges()
      });
    });
  }

  show() {
    this.ishidden = !this.ishidden
    if (this.ishidden == true) {
      this.passtext = "password";
      $('#pshide').html("Show");
    }
    else {
      this.passtext = "text";
      $('#pshide').html("Hide");

    }
  }

  show1() {
    this.ishidden1 = !this.ishidden1;
    if (this.ishidden1 == true) {
      this.passtext1 = "password";
      $('#pshide1').html("Show");
    }
    else {
      this.passtext1 = "text";
      $('#pshide1').html("Hide");

    }
  }

  showPaymentSection() {
    if ($('#stripe').is(':checked')) {
      $('#stripeSection').prop('hidden', false)
      $('#submitPlan').prop('hidden', false)
      $('#paypalSection').prop('hidden', true)
    }
    else {
      $('#stripeSection').prop('hidden', true)
      $('#submitPlan').prop('hidden', true)
      $('#paypalSection').prop('hidden', false)
      const data = {
        plan_id: this.plan_id.toString()
      }
      this.dataService.post('guest/subscribe/paypal', data).then((result) => {
        if (result.link) {
          $('#clickPayPal').prop('hidden', false)
          $('#paypalLink').attr('href', result.link)
        }
        this.paypalInterval = setInterval(() => {
          this.verify();
        }, 5000);
      })
    }
  }

  updatePlan() {
    const data = {
      plan: this.level
    }

    this.dataService.post('host/update-plan-info', data).then((result) => {
      if (result.success = true) {
        localStorage.setItem('role', 'host');
        localStorage.setItem('data', JSON.stringify(result.data));
        localStorage.setItem('plan', result.data.plan);
        this.router.navigate(['/hostportal/offerlist']);
      }
      else {
        Swal.fire({
          position: 'center',
          icon: 'error',
          text: 'Something went wrong. Please try again.',
          showConfirmButton: false,
          timer: 2500
        })
        this.cdf.detectChanges()
        return
      }
    },
      error => {
        Swal.fire({
          position: 'center',
          icon: 'error',
          text: 'Something went wrong. Please try again.',
          showConfirmButton: false,
          timer: 2500
        })
        this.cdf.detectChanges()
        return
      })
  }

  verify() {
    this.dataService.get('guest/verify/paypal/success').then((result) => {
      if (result.success == true && result.data == 1) {
        $('#plan').modal('hide')
        clearInterval(this.paypalInterval);
        this.updatePlan();
        this.planForm = this._formBuilder.group({
          plan_id: null,
          type: 'card',
          number: null,
          month: null,
          year: null,
          cvc: null
        })
      }
    })
  }

  defaultPlan(level) {
    const data = {
      plan: level
    }

    this.dataService.post('host/update-plan-info', data).then((result) => {
      if (result.success = true) {
        localStorage.setItem('role', 'host');
        localStorage.setItem('plan', level);
        this.router.navigate(['/hostportal/offerlist']);
      }
      else {
        Swal.fire({
          position: 'center',
          icon: 'error',
          text: 'Something went wrong. Please r',
          showConfirmButton: false,
          timer: 2500
        })
        this.cdf.detectChanges()
        return
      }
    },
      error => {
        Swal.fire({
          position: 'center',
          icon: 'error',
          text: 'Something went wrong. Please try again.',
          showConfirmButton: false,
          timer: 2500
        })
        this.cdf.detectChanges()
        return
      })
  }

  readAllNotifications() {
    this.dataService.get('read-all-notification').then((result) => {
      this.unread_notifications = 0;
      $('.readAllNotifications').css("background-color", "white");
      this.cdf.detectChanges()
    })
  }

  readNotification(id, status, navigation) {
    if (status != 1) {
      const data = {
        id: id
      }
      this.dataService.post('read-notification', data).then((result) => {
        if (this.unread_notifications != 0) {
          this.unread_notifications = this.unread_notifications - 1;
          this.cdf.detectChanges();
        }
        $('#' + id + 'bg').css("background-color", "white");
      })
    }
    this.cdf.detectChanges();
    if (navigation.includes('dispute') || navigation.includes('withdraw') || navigation.includes('chat')) { //for all these will navigate to booking list
      this.router.navigate(['/guestportal/bookinglist'])
    }
  }

  loginWithApple() {
    this.appleService.signIn();
  }
}
