import { Injectable } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { JwtHelperService } from "@auth0/angular-jwt";
import { DataService } from './data.services';
import Swal from 'sweetalert2';

declare var require: any
@Injectable({
    providedIn: 'root'
})
export class AppleLoginService {

    private readonly appleScriptUrl: string = 'https://appleid.cdn-apple.com/appleauth/static/jsapi/appleid/1/en_US/appleid.auth.js';
    private ready: Promise<boolean> = new Promise(resolve => {
        if (typeof window !== 'undefined') {
            const script = require('scriptjs');
            script(this.appleScriptUrl, () => resolve(true));
        } else {
            resolve(false);
        }
    });

    /**
     * Error details
     */
    signIn() {
        let data = this.init()
        return data
    }

    constructor(private routers: ActivatedRoute,
        private router: Router,
        private jwtHelper: JwtHelperService,
        private dataService: DataService) {
        this.ready.then(isReady => {
            if (isReady) {
                // this.init();
            }
        });
    }

    /**
     * Call init method
     */
    private init(): void {
        const CLIENT_ID = "com.onerooms.bundle.backend"
        const REDIRECT_API_URL = "https://onerooms-socket1.herokuapp.com/Api/Auth/AuthAppleSignin"
        window.open(`https://appleid.apple.com/auth/authorize?client_id=${CLIENT_ID}&redirect_uri=${encodeURIComponent(REDIRECT_API_URL)}&response_type=code id_token&scope=name email&response_mode=form_post&usePopup: ${true}`, '_blank')
        window.addEventListener('message', (event) => {
            const decodedToken = this.jwtHelper.decodeToken(event.data.id_token);
            let requestData = {}
            if (event.data.user) {
                const userName = JSON.parse(event.data.user);
                requestData = {
                    "email": decodedToken.email,
                    "name": `${userName.name.firstName} ${userName.name.lastName}`,
                    "socialId": decodedToken.sub,
                    fcm_token: localStorage.getItem('fcm_token')
                };
            } else {
                requestData = {
                    "email": decodedToken.email,
                    "socialId": decodedToken.sub,
                    fcm_token: localStorage.getItem('fcm_token')
                };
            }

            this.dataService.post('apple-login', requestData).then((result) => {
                if (result.success == true) {
                    localStorage.setItem('token', result.data.token);
                    localStorage.setItem('role', result.data.user.role);
                    localStorage.setItem('id', result.data.user.id);
                    if(result.data.user.role == 'guest'){
                        this.router.navigate(['/guestportal/bookinglist']);
                    }
                    else{
                        this.router.navigate(['/hostportal/offerlist']);
                    }
                }
                else {
                    Swal.fire({
                        position: 'center',
                        icon: 'error',
                        text: 'Something went wrong. Please try agian.',
                        showConfirmButton: false,
                        timer: 2500
                    })
                }
            });
        })
    }

}